import * as React from 'react'
import Logo from '../components/Logo/Logo'
import Navigation from '../components/Navigation/Navigation'
import Footer from '../components/Footer/Footer'
import Hero from '../components/Hero/Hero';
import Button from 'react-bootstrap/Button';
import { Script } from "gatsby"
import Nav from 'react-bootstrap/Nav';
import 'bootstrap/dist/css/bootstrap.min.css'
import './adopt.css'

declare global {
  namespace JSX {
    interface IntrinsicElements {
      ['pet-scroller']: React.DetailedHTMLProps<React.HTMLAttributes<HTMLElement>, HTMLElement>,

    }
  }
}

declare module 'react' {
  export interface HTMLAttributes<T> {
    type?: any,
    age?: any,
    limit?: any,
    hidebreed?: any,
    status?: any,
    petlisttitle?: any
    organization?: any
    apibase?: any
    petfinderurl?: any
    accesstoken?: any
  }
}

const AdoptPage = () => {
  return (
    <div>
      <Logo></Logo>
      <Navigation menuSelected="adopt-menu"></Navigation>
      <main>
        <Hero slideLabel="" slideDescription="" heroImageClass="adopt-hero"></Hero>
        <div className="adopt-content-container">
          <h2>Available Cats &amp; Kittens</h2>
          <p>Thank you for your interest in adopting.  Please review our adoption process and policy.</p>
          <p className="adopt-button-container">
            <a href="#adoption-process"><Button variant="secondary">Adoption Process</Button></a>
            <a href="#adoption-policy"><Button variant="secondary">Adoption Policy</Button></a>
          </p>
          <pet-scroller
            type='[]'
            age='[]'
            limit="24"
            hidebreed
            status="adoptable"
            petlisttitle="Finn&#x27;s Furever Feline Rescue Adoptable Cats"
            organization="NY1625"
            apibase="https://api.petfinder.com"
            petfinderurl="https://www.petfinder.com"
            accesstoken="eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiJ9.eyJhdWQiOiJtd1NmUDQ1SEpPckFSS0RkVGM1M3JGSjNHVTJIdnk0SUxVSGR5Y3NnNGpURzRCVHIzdCIsImp0aSI6Ijc2SmJUc3NVeFFPd1hiM1hhRG5KbUp1RnZVeTZITEVuSllaUGpzQ3oiLCJpYXQiOjE2OTY3MjYzMDIsIm5iZiI6MTY5NjcyNjMwMiwiZXhwIjo0ODUyMzk5OTAyLCJzdWIiOiIxNTM3NzYyNSIsInNjb3BlcyI6W119.cYKI9Eqzalo6OLUgSf7fgqUa2IoIBr6o7E6Hz6inHMxFNrz7VigzJXKzJyIGqd7Av-15p4Vww8CJFrVr7n38dHgX8vQzhmf5Qg7F2RDyNoa_vl68Z-GRU2I9mZTGqOLSTd9Y2YXlWBK6DOM3auK16Xk9EKTuxjsR0oo2vLJWY_jNUb94ubB36CWl4Y1DvFBt9x0NLb9vJ3QAKfOwtJXQwnOgk7PdWfCrJAS2g-SsJh85z3qZsqgeN-xPineUHXNVkgfu8o1zEbLtgkZbNYTh-Ka1IenInZYiZMT9hYq6sQ7lzsMCg1OtPzqpAZB-rDHHa14euKPeXYR-gfp0qwZQVA">
          </pet-scroller>
          <Script src="https://www.petfinder.com/assets/widgets/scripts/main-widgets-web.js" />
          <hr />
          <h2 id="adoption-process">Adoption Process</h2>
          <ol className="adoption-process">
            <li>Please complete an <a href="/images/Adoption_Application.docx">application</a> for a specific cat or cats and submit it to <a href="mailto:finnsfureverfelinerescue@yahoo.com">finnsfureverfelinerescue@yahoo.com</a>. If you 
            prefer to fill out our application online, please use this <a  href="/adoption-form">link</a>.</li>
            <li>After we review your application and reach out to your veterinary and landlord (if applicable), we'll contact you for a 
              phone interview and schedule a meet and greet with the cat(s) you specified on your application.
            </li>
            <li>As the last step of finalizing the adoption, you'll review and sign the adoption contract.</li>
          </ol>
          <hr />
          <h2 id="adoption-policy">Adoption Policy</h2>
        
          <p><strong>Single Kittens: </strong>In most cases we do not adopt single kittens under 6 months to homes who do not already
            have another young, friendly, active feline. This is not about placing more kittens and cats. It's about the future of 
          the individual kittens, their habits and their happiness. Many people have been skeptical about adopting two kittens 
          rather than one only to thank us later for sticking to our policy. The rewards are great. We've never had anyone tell 
          us they wished they had only adopted one.</p>

          <p><strong>Family Decision: </strong>Having a pet should be a pleasant and rewarding experience for all involved. 
          If you have small children, please bring them with you when you come for your adoption meet and greet. Because small 
          kittens are vulnerable and young children may be unable to predict the consequences of their actions, Finn's Furever Feline 
          Rescue takes great care in placing kittens in homes with young children. Finn's Furever Feline Rescue often has cats who 
          have shown themselves to be particularly tolerant of children. We will work with you to determine the best fit for your 
          family.</p>

          <p><strong>Other Pets: </strong>We do our best to place each cat into a home where the family bond can be successful. 
          To that end, our adoption process includes specific advice regarding introduction to other pets. It is possible that 
          the cat in whom you are interested, may not be deemed an appropriate match for your furry family. If that is the case, 
          please understand the decision was made in the best interest of the cat and your other pets. All resident pets must be 
          spayed or neutered prior to an adoption from Finn's Furever Feline Rescue.</p>

          <p><strong>Landlord Approval: </strong>We verify that your rental housing is pet friendly to protect your cat and you
            from being evicted.</p>

          <p><strong>Age/Employment: </strong>Finn's Furever Feline Rescue seeks to adopt to those who are financially secure and 
          in a stable living situation in order to maximize the likelihood of a lifelong bond. Older teens, newly on their own, or 
          still living with parents, may find themselves, in a year or two, making life changes that result in their no longer being 
          able to care for their pet. Because of these reasons and as our desire is to find forever homes for our cats, we only adopt 
          to adults 21 years and older. Additionally, as there may be, at some time, significant veterinary costs involved with any pet, 
          we ask that people adopt only if they have seriously evaluated the meaning of a twenty year commitment and are 
          financially able to bear the cost of future emergencies.</p>


          <p><strong>Application: </strong>Finn's Furever Feline Rescue must have an approved Adoption application filled out
          and signed. We will then setup a phone interview and a meet and greet. Please take a look at our <a href="#adoption-process">Adoption Process</a> for information about completing
          and submitting your application.</p>

          <p><strong>Indoor Only: </strong>Finn's Furever Feline Rescue adopts to indoor-only homes. This means INDOORS ALL OF 
          THE TIME. If letting your cat outdoors does not suit your household, please let us refer you to other organizations
          which have a more tolerant contract around this restriction.</p>

          <p><strong>Declawing:</strong> Cats frequently suffer long term problems after being surgically declawed, such 
          as aggressive and eliminating outside the litter box. Finn's Furever Feline Rescue requires that you sign a contract 
          agreeing that you will not have the cat's claws surgically altered in any way, including laser surgery or tendonotomy.</p>
        
        </div>
      </main>
      <Footer></Footer>
    </div>
  )
}

export const Head = () => <title>Adopt - Finn's Furever Feline Rescue</title>
export default AdoptPage